<template>
  <div>
    <img class="bg" src="@/assets/img/bg.jpg">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="login-form">
      <h1 class="h1"></h1>
                              <el-form-item v-if="tableName=='xuesheng'" label="" prop="xueshengxueshengxuehao">
        <el-input v-model="ruleForm.xueshengxuehao" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='xuesheng'" label="" prop="xueshengxueshengxingming">
        <el-input v-model="ruleForm.xueshengxingming" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='xuesheng'" label="" prop="xueshengmima">
        <el-input v-model="ruleForm.mima" placeholder=""></el-input>
      </el-form-item>
                                          <el-form-item v-if="tableName=='xuesheng'" label="" prop="xueshengnianji">
        <el-input v-model="ruleForm.nianji" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='xuesheng'" label="" prop="xueshengbanji">
        <el-input v-model="ruleForm.banji" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='xuesheng'" label="" prop="xueshengshenfenzheng">
        <el-input v-model="ruleForm.shenfenzheng" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='xuesheng'" label="" prop="xueshenglianxidianhua">
        <el-input v-model="ruleForm.lianxidianhua" placeholder=""></el-input>
      </el-form-item>
                                                <el-form-item v-if="tableName=='jiaoshi'" label="" prop="jiaoshijiaoshigonghao">
        <el-input v-model="ruleForm.jiaoshigonghao" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='jiaoshi'" label="" prop="jiaoshijiaoshixingming">
        <el-input v-model="ruleForm.jiaoshixingming" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='jiaoshi'" label="" prop="jiaoshimima">
        <el-input v-model="ruleForm.mima" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='jiaoshi'" label="" prop="jiaoshizhicheng">
        <el-input v-model="ruleForm.zhicheng" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='jiaoshi'" label="" prop="jiaoshilianxidianhua">
        <el-input v-model="ruleForm.lianxidianhua" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='jiaoshi'" label="" prop="jiaoshishenfenzheng">
        <el-input v-model="ruleForm.shenfenzheng" placeholder=""></el-input>
      </el-form-item>
                                                                                                                                                                                                      <el-button @click="login()" class="btn-login" type="primary"></el-button>
    </el-form>
  </div>
</template>
<script>
export default {
  data() {
    return {
      ruleForm: {
              },
      tableName:"",
      rules: {}
    };
  },
  mounted(){
    let table = this.$storage.get("loginTable");
    this.tableName = table;
  },
  methods: {
    // uuid
    getUUID () {
      return new Date().getTime();
    },
    // 
    login() {
                                                                                                                                                                                                                                                                                                                                                            if(`xuesheng` == this.tableName && this.ruleForm.shenfenzheng&&(!this.$validate.checkIdCard(this.ruleForm.shenfenzheng))){
        this.$message.error(``);
        return
      }
                                                if(`xuesheng` == this.tableName && this.ruleForm.lianxidianhua&&(!this.$validate.isMobile(this.ruleForm.lianxidianhua))){
        this.$message.error(``);
        return
      }
                                                                                                                                                                                                                                                                                    if(`jiaoshi` == this.tableName && this.ruleForm.lianxidianhua&&(!this.$validate.isMobile(this.ruleForm.lianxidianhua))){
        this.$message.error(``);
        return
      }
                                                                        if(`jiaoshi` == this.tableName && this.ruleForm.shenfenzheng&&(!this.$validate.checkIdCard(this.ruleForm.shenfenzheng))){
        this.$message.error(``);
        return
      }
                                                                                                                                                                                                            this.$refs["ruleForm"].validate(valid => {
        if (valid) {
          this.$http({
            url: `${this.tableName}/register`,
            method: "post",
            data:this.ruleForm
          }).then(({ data }) => {
            if (data && data.code === 0) {
              this.$router.replace({ path: "/login" });
            } else {
              this.$message.error(data.msg);
            }
          });
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
</style>
