<template>
  <div>
    <el-form
      class="detail-form-content"
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="80px"
    >
      <el-row>
                  <el-col :span="12">
        <el-form-item  v-if="type!='info'"  label="" prop="ketitimu">
          <el-input v-model="ruleForm.ketitimu" 
              placeholder="" clearable></el-input>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.ketitimu" label="" prop="ketitimu">
              <el-input v-model="ruleForm.ketitimu" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="type!='info'" label="" prop="kaishishijian">
            <el-date-picker
                format="yyyy  MM  dd "
                value-format="yyyy-MM-dd"
                v-model="ruleForm.kaishishijian" 
                type="date"
                placeholder="">
            </el-date-picker> 
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.kaishishijian" label="" prop="kaishishijian">
              <el-input v-model="ruleForm.kaishishijian" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="type!='info'" label="" prop="jiezhishijian">
            <el-date-picker
                format="yyyy  MM  dd "
                value-format="yyyy-MM-dd"
                v-model="ruleForm.jiezhishijian" 
                type="date"
                placeholder="">
            </el-date-picker> 
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.jiezhishijian" label="" prop="jiezhishijian">
              <el-input v-model="ruleForm.jiezhishijian" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                                          <el-col :span="12">
        <el-form-item  v-if="type!='info'"  label="" prop="xueshengxuehao">
          <el-input v-model="ruleForm.xueshengxuehao" 
              placeholder="" clearable></el-input>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.xueshengxuehao" label="" prop="xueshengxuehao">
              <el-input v-model="ruleForm.xueshengxuehao" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="type!='info'"  label="" prop="xueshengxingming">
          <el-input v-model="ruleForm.xueshengxingming" 
              placeholder="" clearable></el-input>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.xueshengxingming" label="" prop="xueshengxingming">
              <el-input v-model="ruleForm.xueshengxingming" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                        <el-col :span="12">
        <el-form-item v-if="type!='info'"  label="" prop="jiaoshigonghao">
          <el-select v-model="ruleForm.jiaoshigonghao" placeholder="">
            <el-option
                v-for="(item,index) in jiaoshigonghaoOptions"
                v-bind:key="index"
                :label="item"
                :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.jiaoshigonghao" label="" prop="jiaoshigonghao">
              <el-input v-model="ruleForm.jiaoshigonghao" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                        <el-col :span="12">
        <el-form-item v-if="type!='info'" label="" prop="tijiaoshijian">
            <el-date-picker
                value-format="yyyy-MM-dd HH:mm:ss"
                v-model="ruleForm.tijiaoshijian" 
                type="datetime"
                placeholder="">
            </el-date-picker>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.tijiaoshijian" label="" prop="tijiaoshijian">
              <el-input v-model="ruleForm.tijiaoshijian" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                                                            </el-row>
                                                                                                                                                                                                                    <el-row>
            <el-col :span="24">
              <el-form-item v-if="type!='info'"  label="" prop="zhongqibaogao">
                <editor 
                    style="min-width: 200px; max-width: 600px;"
                    v-model="ruleForm.zhongqibaogao" 
                    class="editor" 
                    action="file/upload">
                </editor>
              </el-form-item>
              <div v-else>
                <el-form-item v-if="ruleForm.zhongqibaogao" label="" prop="zhongqibaogao">
                    <span v-html="ruleForm.zhongqibaogao"></span>
                </el-form-item>
              </div>
            </el-col>
          </el-row>
                                                                                                        <el-form-item>
                <el-button v-if="type!='info'" type="primary" @click="onSubmit"></el-button>
        <el-button v-if="type!='info'" @click="back()"></el-button>
        <el-button v-if="type=='info'" @click="back()"></el-button>
      </el-form-item>
    </el-form>
    
    
  </div>
</template>
<script>
// url
import { isNumber,isIntNumer,isEmail,isMobile,isURL,checkIdCard } from "@/utils/validate";
export default {
  data() {
    let self = this
    var validateIdCard = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!checkIdCard(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    var validateUrl = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!isURL(value)) {
        callback(new Error("URL"));
      } else {
        callback();
      }
    };
    var validatePhone = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!isMobile(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    var validateEmail = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!isEmail(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    var validateNumber = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!isNumber(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    var validateIntNumber = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!isIntNumer(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    return {
      id: '',
      type: '',
            ruleForm: {
                        ketitimu: '',
                                kaishishijian: '',
                                jiezhishijian: '',
                                zhongqibaogao: '',
                                xueshengxuehao: '',
                                xueshengxingming: '',
                                jiaoshigonghao: '',
                                tijiaoshijian: '',
                                sfsh: '',
                                shhf: '',
                      },
                                                                                                            jiaoshigonghaoOptions: [],
                                                              rules: {
                  ketitimu: [
                                                                                              ],
                  kaishishijian: [
                                                                                              ],
                  jiezhishijian: [
                                                                                              ],
                  zhongqibaogao: [
                                                                                              ],
                  xueshengxuehao: [
                                                                                              ],
                  xueshengxingming: [
                                                                                              ],
                  jiaoshigonghao: [
                                                                                              ],
                  tijiaoshijian: [
                                                                                              ],
                  sfsh: [
                                                                                              ],
                  shhf: [
                                                                                              ],
              }
    };
  },
  props: ["parent"],
  computed: {
                                                                                                                              },
  methods: {
        // 
    download(file){
      window.open(`${file}`)
    },
    // 
    init(id,type) {
      if (id) {
        this.id = id;
        this.type = type;
      }
      if(this.type=='info'||this.type=='else'){
        this.info(id);
      }else if(this.type=='cross'){
        var obj = this.$storage.getObj('crossObj');
        for (var o in obj){
                    if(o=='ketitimu'){
            this.ruleForm.ketitimu = obj[o];
            continue;
          }
                    if(o=='kaishishijian'){
            this.ruleForm.kaishishijian = obj[o];
            continue;
          }
                    if(o=='jiezhishijian'){
            this.ruleForm.jiezhishijian = obj[o];
            continue;
          }
                    if(o=='zhongqibaogao'){
            this.ruleForm.zhongqibaogao = obj[o];
            continue;
          }
                    if(o=='xueshengxuehao'){
            this.ruleForm.xueshengxuehao = obj[o];
            continue;
          }
                    if(o=='xueshengxingming'){
            this.ruleForm.xueshengxingming = obj[o];
            continue;
          }
                    if(o=='jiaoshigonghao'){
            this.ruleForm.jiaoshigonghao = obj[o];
            continue;
          }
                    if(o=='tijiaoshijian'){
            this.ruleForm.tijiaoshijian = obj[o];
            continue;
          }
                    if(o=='sfsh'){
            this.ruleForm.sfsh = obj[o];
            continue;
          }
                    if(o=='shhf'){
            this.ruleForm.shhf = obj[o];
            continue;
          }
                  }
                                                                                                                                                                              }
            // 
      this.$http({
        url: `${this.$storage.get('sessionTable')}/session`,
        method: "get"
      }).then(({ data }) => {
        if (data && data.code === 0) {
          var json = data.data;
                                                                                                                            this.ruleForm.xueshengxuehao = json.xueshengxuehao
                                                this.ruleForm.xueshengxingming = json.xueshengxingming
                                                                                                                      } else {
          this.$message.error(data.msg);
        }
      });
                                                                                                                                                      this.$http({
              url: `option/jiaoshi/jiaoshigonghao`,
              method: "get"
            }).then(({ data }) => {
              if (data && data.code === 0) {
                this.jiaoshigonghaoOptions = data.data;
              } else {
                this.$message.error(data.msg);
              }
            });
         
                                                                                  },
                                                                                        // 
                                                                                                                info(id) {
      this.$http({
        url: `zhongqibaogao/info/${id}`,
        method: "get"
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.ruleForm = data.data;
        } else {
          this.$message.error(data.msg);
        }
      });
    },
        // 
    onSubmit() {
                  // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                                                                                                                                                                                                                        this.$refs["ruleForm"].validate(valid => {
        if (valid) {
          this.$http({
            url: `zhongqibaogao/${!this.ruleForm.id ? "save" : "update"}`,
            method: "post",
            data: this.ruleForm
          }).then(({ data }) => {
            if (data && data.code === 0) {
              this.$message({
                message: "",
                type: "success",
                duration: 1500,
                onClose: () => {
                  this.parent.showFlag = true;
                  this.parent.addOrUpdateFlag = false;
                  this.parent.zhongqibaogaoCrossAddOrUpdateFlag = false;
                  this.parent.search();
                }
              });
            } else {
              this.$message.error(data.msg);
            }
          });
        }
      });
    },
    // uuid
    getUUID () {
      return new Date().getTime();
    },
    // 
    back() {
      this.parent.showFlag = true;
      this.parent.addOrUpdateFlag = false;
      this.parent.zhongqibaogaoCrossAddOrUpdateFlag = false;
    },
                                                                                                                              }
};
</script>
<style lang="scss">
.editor{
  height: 500px;
}
.amap-wrapper {
  width: 100%;
  height: 500px;
}
.search-box {
  position: absolute;
}
</style>
